// import {Btn} from './components/Btn'
import './App.css'
import { useState } from 'react';
 let res1='0' ;
function App(){
  const btn=[
  ['C','Del','%','+'],
  [7,8,9,'/'],
  [4,5,6,'*'],
  [1,2,3,'-'],
  [0,'.','!','=']
  ];
let [res,setres]=useState('0');
const screen=document.getElementsByClassName("screen-output")[0];
function factorial(n){
  if (n < 0) return;
  if (n < 2) return 1;
  return n * factorial(n - 1);
}

   const btns=btn.map((row,i) => (
    <div className="calc-row"  key={i}>
    {row.map((ele,j)=>(
        <div className="button" key={j}  style={ele==='='? {background: 'dodgerblue'}:{}&&ele==='Del'? {color: '#ff6347'}:{}  }   onClick={()=>{
          
          if(ele==='C'){
            res1='0';
            setres(res1);
            // res1='';
            screen.style.display="none";
          }
          else{if(ele==='Del'){
            if(res1.length<=1){
              res1='0'
             
              setres(res1);
              // res1='';
              screen.style.display="none";
          }
            else{
            res1=res1.substring(0, res1.length - 1);
            setres(res1);
            screen.style.display="none";
          }
          }
          else{if(ele==='='){
            let ref=eval(res1);
            setres( (ref / 1000000000000000<=1)?ref:Infinity)
            screen.style.display="inline"; 
          }
          else{
            if(ele==='!'){
              let ref1=eval(res1);
              let ref=factorial(parseInt(ref1));
              let fer=(ref / 1000000000000000 <=1)?ref:Infinity;
              console.log(fer);
              setres(fer);
              screen.style.display="inline";
             }
             else{
               console.log(res1);
               if(res1[0]==='0'){
                res1=res1.substring(1, res1.length );
               }
               res1=res1+ele;
                setres(res1)
            
            
            screen.style.display="none";
             }
          }  
        }
      }
      }
        }>
       {ele}
        </div>
    ))}
    <br/>
    </div>
  ))
  return (
    
    <body>
    <section className="container">
    <div className="calc-head">
        <h1>Calculator</h1>
        <h4>By H'mila Houssein</h4>
       </div>
            <div className="calc-body">
            <div className="calc-screen">
            <div className="screen-content">
            <h2>{res1}</h2> 
            
            </div>
            <div className="screen-output" style={{display:"none"}}>
              <h1>=</h1> 
            <h2>{res}</h2>
                </div> 
                </div>    
                   {btns}
                </div>
      </section>
    </body>
  );
}
export default App;
